import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
} from "@material-ui/core";
import React from "react";
import FlagIcon from "@material-ui/icons/Flag";
import CheckIcon from "@material-ui/icons/Check";
import ClearIcon from "@material-ui/icons/Clear";
import moment from "moment";
import { ItemResults } from "../Data/Items";

export interface ItemsTableProperties {
  sectionId: string;
}

export const ItemsTable = ({ sectionId }: ItemsTableProperties) => {
  const itemData = ItemResults.filter((i) => i.section === sectionId);

  return (
    <Table>
      <TableHead>
        <TableRow>
          <TableCell>
            <Typography>#</Typography>
          </TableCell>
          <TableCell>
            <CheckIcon />
          </TableCell>
          <TableCell>
            <Typography>Preview</Typography>
          </TableCell>
          <TableCell>
            <Typography>Time</Typography>
          </TableCell>
          <TableCell>
            <FlagIcon />
          </TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
        {itemData.map((item) => (
          <TableRow key={item.number}>
            <TableCell>
              <Typography>{item.number}</Typography>
            </TableCell>
            <TableCell>
              {item.correct ? <CheckIcon /> : <ClearIcon />}
            </TableCell>
            <TableCell>
              <Typography>{item.preview}</Typography>
            </TableCell>
            <TableCell>
              <Typography>
                {moment
                  .utc(
                    moment
                      .duration(item.timeInSeconds, "seconds")
                      .asMilliseconds()
                  )
                  .format("m:ss")}
              </Typography>
            </TableCell>
            <TableCell>{item.flagged && <FlagIcon />}</TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
};
