import {
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
} from "@material-ui/core";
import React from "react";

const useStyles = makeStyles({
  acronym: {
    width: "10%",
  },
  description: {},
  items: {
    width: "10%",
  },
  correct: {
    width: "10%",
  },
});

export interface SkillsBreakdownTableProperties {
  skills: Array<{
    label: string;
    description: string;
    items: number;
    correct: number;
  }>;
}

export const SkillsBreakdownTable = ({
  skills,
}: SkillsBreakdownTableProperties) => {
  const classes = useStyles();

  return (
    <Table>
      <TableHead>
        <TableRow>
          <TableCell className={classes.acronym}>
            <Typography>Acronym</Typography>
          </TableCell>
          <TableCell className={classes.description}>
            <Typography>Description</Typography>
          </TableCell>
          <TableCell className={classes.items}>
            <Typography>Items</Typography>
          </TableCell>
          <TableCell className={classes.correct}>
            <Typography>Correct</Typography>
          </TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
        {skills.map((skill, index) => (
          <TableRow key={index}>
            <TableCell>
              <Typography>{skill.label}</Typography>
            </TableCell>
            <TableCell>
              <Typography>{skill.description}</Typography>
            </TableCell>
            <TableCell>
              <Typography>{skill.items}</Typography>
            </TableCell>
            <TableCell>
              <Typography>{skill.correct}</Typography>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
};
