import { Box, makeStyles, Paper, Typography } from "@material-ui/core";
import React from "react";
import { CandidateHighlights } from "../Data/Highlights";

const useStyles = makeStyles({
  itemHighlight: {
    padding: 15,
    marginBottom: 15,
  },
  itemHighlightHeader: {
    margin: 5,
  },
  highlightContent: {
    borderLeft: "3px solid gold",
    marginTop: 10,
    marginBottom: 20,
    marginLeft: 25,
    paddingTop: 1,
    paddingBottom: 1,
  },
});

export interface HighlightsProperties {
  sectionId: string;
}

export const Highlights = ({ sectionId }: HighlightsProperties) => {
  const classes = useStyles();
  const itemHighlights = CandidateHighlights.filter(
    (h) => h.section === sectionId
  );

  return (
    <>
      {itemHighlights.map((itemHighlight) => (
        <Paper
          elevation={3}
          key={`item-${itemHighlight.number}`}
          className={classes.itemHighlight}
        >
          <Typography variant="h6" className={classes.itemHighlightHeader}>
            Item {itemHighlight.number}
          </Typography>
          {itemHighlight.highlights.map((highlight, index) => (
            <Box key={index} className={classes.highlightContent}>
              <blockquote
                dangerouslySetInnerHTML={{ __html: highlight }}
              ></blockquote>
            </Box>
          ))}
        </Paper>
      ))}
    </>
  );
};
